import scrollTo from 'react-scroll-to-component'

class Nav extends React.Component {

	constructor(props) {
		super(props)
	}
	
	render () {
		return (
			<nav className="main-nav">
					{this.props.links.map((link, i) => {
						return (
							<a className="main-nav__link" href={link.href} onClick={this.handleClick}>{link.name}</a>
						)
					})}
			</nav>
		)
	}

}

export default Nav
